.question {
    width: 80%;
    height: 100vh;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 20px;
    overflow: hidden;
}

.option {
    width: 45%;
    background-color: rgb(151, 102, 10);
    /* padding: 20px 10px; */
    margin: 10px;
    font-size: 200%;
    padding-top: 20vh;
    font-weight: 300;
    text-transform: capitalize;
    color: #fff;
}

.option:hover {
    background-color: rgb(209, 167, 87);
}