.App {
  text-align: center;
 
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.grid {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.firebase {
  width: 49%;
}

.qsocket {
  width: 49%;
  border-left: 1px solid #ccc;
}



.action {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 20px;
}

.action input[type=text] {
  width: 250px;
  height: 30px;
}


.action input[type=button] {
  height: 35px;
  width: 50px;
  text-transform: capitalize;
  border: 0px;
  cursor: pointer;
}